.about {
  @include pageGrid;

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      align-self: center;
      grid-column: 1 / 6;
    }
  }

  &__info {
    grid-column: 1 / -1;
    margin: 0;
    font-weight: 500;
    font-size: 23px;
    line-height: 32.2px;

    @include onTablet {
      grid-column: 4 / -1;
    }

    @include onDesktop {
      grid-column: 6 / -1;
    }
  }

  @include onTablet {
    // grid-template-columns: repeat(2, 1fr);
    row-gap: 24px;
  }
}
