.product {
  font-weight: 700;
  font-size: 24px;
  line-height: 33.6px;

  &__photo {
    width: 100%;
    transition: all 0.3s ease;

    &:hover {
      transform: scale(1.05);
      box-shadow: 1px 4px 16px 0px #e8e8f066;
      border-radius: 10px;
    }
  }

  &__title {
    margin-top: 32px;
    margin-bottom: 8px;
  }

  &__info {
    font-weight: 400;
    font-size: 16px;
    line-height: 22.4px;
  }
}
