.compare {
  &__products {
    @include pageGrid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
    @include onDesktop {
      column-gap: 28px;
    }
  }

  &__product {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 /-2;
    }
    @include onDesktop {
      grid-column: span 4;
    }
  }
}
