/* #region menu */

.menu {
  box-sizing: border-box;
  height: 100vh;
  z-index: 5;

  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;

  
    overflow-y: auto;



  &__nav {
    margin-bottom: 40px;
  }


  &__phone--number {
    display: block;
    margin-bottom: 16px;
    text-decoration: none;
    color: #1d1d1d;
    font-size: 16px;
    line-height: 27px;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;

    text-decoration: none;
    color: #1d1d1d;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }
}


/* #endregion */
