/* #region header */
.header {
  background-image: url('./../images/header-bg-mobile.jpg');
  background-size: cover;
  background-position: right;

  @include onTablet {
    background-image: url('./../images/header-bg-tablet.jpg');
  }
  @include onDesktop {
    background-image: url('./../images/header-bg-desktop.jpg');
  }

  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__title {
    margin: 0;
    text-align: center;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 40px;
    }

    @include onDesktop {
      font-size: 80px;
    }
  }

  &__bottom {
    padding-bottom: 88px;

    @include onTablet {
      padding-bottom: 130px;
    }
  }
}

button,
a{
  cursor: pointer;
}

/* #endregion */
