.details {
  &__wrapper {
    display: grid;
    row-gap: 40px;

    @include onTablet {
      row-gap: 48px;
    }
  }
}

.detail {
  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
    column-gap: 20px;
  }

  &__link {
    position: relative;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }
      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }
      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.3s ease-in;
  }

  &__photo:hover {
    transform: scale(1.05);
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 33.6px;
  }

  &__info {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 22.4px;
  }

  &__button {
    color: #1d1d1d;
    box-sizing: border-box;
    height: 56px;
    font-family: Popins, sans-serif;
    justify-self: center;
    border-radius: 40px;
    background-color: #ffffff;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    border: 1px solid #fff;
    padding: 14.5px 108.5px;

    transition: all 0.3s ease-in;

    @include onTablet {
      padding: 14.5px 165.5px;
    }
    @include onDesktop {
      padding: 14.5px 131.5px;
    }
  }

  &__button:hover {
    transform: translateY(5px);
    border: 1px solid #000;
    background-color: #1d1d1d;
    color: #fff;
  }
}
