.contact {
  &__wrapper {
    display: grid;
    row-gap: 48px;

    @include onTablet {
      @include pageGrid;
      column-gap: 24px;
    }

    @include onDesktop {
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  input,
  textarea:focus {
    outline: none;
  }

  &__forms {
    @include onTablet {
      grid-column: 1 / 4;
    }
    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__name,
  &__email {
    border-radius: 40px;
    padding: 14px 16px;
    border: 1px solid #fff;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }

  &__message {
    padding: 14px 16px;
    border: 1px solid #fff;
    height: 148px;
    border-radius: 16px;
    font-family: Popins, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }

  &__button {
    margin-top: 32px;
    font-family: Popins, sans-serif;
    box-sizing: border-box;
    border: 1px solid #fff;
    background-color: #fff;
    border-radius: 40px;
    width: 100%;
    padding: 14.5px 0;
    justify-content: center;
    align-self: center;
    color: #1d1d1d;
    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    transition: all 0.3s ease;
  }

  &__button:hover {
    box-shadow: 1px 4px 16px 0px #e8e8f066;
  }
  &__button:active {
    box-shadow: 1px 4px 16px 0px #e8e8f066;
  }

  &__desc {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onTablet {
      grid-column: 4 / -1;
    }
    @include onDesktop {
      grid-column: 8 /11;
    }
  }
}

.desc {
  &__text {
    position: relative;
    text-decoration: none;
    color: #fff;
    margin: 0;
    transition: color 0.3s ease;


    &:hover {
      color: #959494;
    }
  }


  &__title {
    margin: 0;
    margin-bottom: 8px;
  }
}

textarea {
  resize: none;
}
